.footer{
    position:relative;
    height: 300px;
    bottom: -300px;
    width: 100%;
    background-color: black;
    color: white;
}
.footer__container{
    max-width: 1600px;
    margin: auto;
    display: flex;
    justify-content: space-between;
}
.footer_logo{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.footer_logo #footer__mainlogo{
    padding: 20px;
    width: 50px;
    object-fit: contain;
}

.footer_logo #footer__deliverylogo{
    padding: 20px;
    width: 200px;
    object-fit: contain;
}

.footer__options{
    display: flex;
    padding: 20px 40px;
}
.footer__options > div > ul {
    list-style: none;
} 

.footer__options > div > ul > li{
    padding: 10px;
} 

@media only screen and (max-width: 650px) {
    .footer__container{
        max-width: 1600px;
        margin: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        align-items: center;
    }
    .footer__options{
        width: 70%;
        display: flex;
        padding: 20px 0px;
    }
    .footer__options > div > ul{
        padding-left: 10px;
    }
    .footer_logo{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    }
}
