.cartmodal{
    width: 400px;
    z-index: 4;
    position: absolute;
    top: 20%;
    left: 40%;
    background-color: white;
    border-radius: 20px;
    padding: 20px;
    box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
    border: 1px solid #f1b104;
}

.cartmodal__background{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    height: 100vh;
    background-color: black;
    opacity: 0.7;
}

.cartmodal > ul {
    margin: 0;
    padding-left: 0;
}
.cartmodal__item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0;
}

.cartmodal__item > p {
    margin: 10px;
}

.cartmodal__item > img {
    width: 50px;
}

.cartmodal__item_input > input{
    width: 30px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type=number] {
    -moz-appearance:textfield; /* Firefox */
}

.cartmodal__button{
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    background-color: #f1b104;
    color: white;
    padding: 10px 20px;
    border-radius: 10px;
    outline: none;
    border: 0px;
}

.cartmodal__qty{
    background-color: #f1b104;
    color: white;
    padding: 3px 10px;
    border: 0px;
    border-radius: 5px;
    margin: 0px 2px;
}

.cartmodal__item_input > input{
    border: #f1b104;
}

@media only screen and (max-width: 650px) {
    .cartmodal{
        width: 400px;
        z-index: 4;
        position: absolute;
        top: 20%;
        left: 5%;
        background-color: white;
        border-radius: 20px;
        padding: 20px;
        box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
        border: 1px solid #f1b104;
    }
}